<template>
    <div class="jbm-topbar">
        <div class="container">
            <div class="row">
                <div class="col-md-6 col-sm-12 col-xs-12 text-left">
                    <ul class="dis-inline list-none jbm-topbar-contact">
                        <li>
                            <a href="tel:+0001234567890"><i class="fa fa-phone"></i>+ 000 123 456 7890</a>
                        </li>
                        <li>
                            <a href="mailto:contactadmin@findcandidate.com"><i class="fa fa-envelope"></i>contactadmin@findcandidate.com</a>
                        </li>
                    </ul>
                </div>
                <div class="col-md-6 col-sm-12 col-xs-12 hidden-xs hidden-sm">
                    <div class="dropdown text-right" v-if="isAuthenticated">
                        <div class="user-menu" type="button" id="userMenu" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                            <span class="glyphicon glyphicon-user"></span>
                            Test User
                            <span class="caret"></span>
                        </div>
                        <ul class="dropdown-menu dropdown-menu-right" aria-labelledby="userMenu">
                            <li><a href="/profile"> <span class="glyphicon glyphicon-user"></span> Profile</a></li>
                            <li><a href="/account-setting"> <span class="glyphicon glyphicon-cog"></span> Settings</a></li>
                            <li role="separator" class="divider"></li>
                            <li><a @click.prevent="logOut()"> <span class="glyphicon glyphicon-log-out"></span> Log Out</a></li>
                        </ul>
                    </div>
                    <ul v-else class="dis-inline list-none jbm-topbar-login text-right">
                        <li>
                            <a class="login-pop" @click="logInOpened=true">Login</a>
                        </li>
                        <li>
                            <a class="register-pop"  @click="signUpOpened=true">Register</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <LogIn :isActive="logInOpened" @closed="logInOpened=false"  @convertSignUp="convertSignUp"></LogIn>
        <SignUp :isActive="signUpOpened" @closed="signUpOpened=false" @convertLogIn="convertLogIn"></SignUp>
    </div>
</template>

<script>

import LogIn from '@/components/Auth/LogIn'
import SignUp from '@/components/Auth/SignUp'
import { mapGetters } from 'vuex'

export default {
  name: 'TopBar',
  components:{
    LogIn,
    SignUp
  },
  data () {
    return {
      logInOpened: false,
      signUpOpened: false
    }
  },
  computed: {
    ...mapGetters(['isAuthenticated'])
  },
  methods: {
    convertLogIn() {
        this.signUpOpened = false
        this.logInOpened = true
    },
    convertSignUp() {
        this.signUpOpened = true
        this.logInOpened = false
    },
    logOut() {
        this.$store.dispatch('logOut')
    }
  }
}
</script>

