import Vue from 'vue'
import Vuex from 'vuex'

import authService from '@/service/authService'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    role: 'candidate',
    userInfo: {},
    isAuthenticated: false
  },
  mutations: {
    setUserData(state, data) {
      state.userInfo = data
    },
    setToken(state, token) {
      if(!token)  {
        state.isAuthenticated =false
      } else {
        state.isAuthenticated = true
      } 
      state.token = token
    }
  },
  getters: {
    isAuthenticated: state=> {
      return state.isAuthenticated
    },
    getRole: state => {
      return state.role
    },
    isCandidate: state => {
      return state.role == 'candidate'
    },
    isEmployer: state => {
      return state.role == 'employer'
    }
  },
  actions: {
    logIn({commit}, data) {
        let exampleToken = "b4j9sdlakd"
        commit('setToken', exampleToken)
        commit('setUserData', data)
        authService.setAuthData(exampleToken)
        authService.setUserData(data)
    },
    logOut({commit}, data) {
      commit('setToken', null)
      commit('setUserData', {})
      authService.cleanAuthData()
    }
  },
  modules: [

  ]
})
