import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Profile from './views/Profile.vue'
import AccountSetting from './views/AccountSettings.vue'
import ContactUs from './views/ContactUs.vue'
import AboutUs from './views/AboutUs.vue'
import Message from './views/Message.vue'
import PaymentHistory from './views/PaymentHistory.vue'
import Candidates from './views/Candidates.vue'
import TermsAndConditions from './views/TermsAndConditions.vue'
import NewJobPost from './views/JobPost.vue'
import ApplyJob from './views/ApplyJob.vue'
import Jobs from './views/Jobs.vue'

import MyJobs from './components/Job/MyJobs.vue'
import CandidateDetail from './components/Candidate/CandidateDetail.vue'
import PasswordAndSecurity from './components/Account/PasswordAndSecurity.vue'
import BillingMethods from './components/Account/BillingMethods.vue'
import NotificationSetting from './components/Account/NotificationSetting.vue'
import TaxInformation from './components/Account/TaxInformation.vue'
import Membership from './components/Account/Membership.vue'
import ShortListedCandidate from './components/Employee/ShortListedCandidate.vue'
import PricingPlan from './components/PricingPlan.vue'
import Faq from './components/Faq.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/contact-us',
      name: 'contact-us',
      component: ContactUs
    },
    {
      path: '/faq',
      name: 'faq',
      component: Faq
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/message',
      name: 'message',
      component: Message
    },
    {
      path: '/payment-history',
      name: 'payment-history',
      component: PaymentHistory
    },
    {
      path: '/short-listed',
      name: 'short-listed',
      component: ShortListedCandidate
    },
    {
      path: '/create-new-job',
      name: 'create-new-job',
      component: NewJobPost
    },
    {
      path: '/jobs/search',
      name: 'job-search',
      component: Jobs
    },
    {
      path: '/job/:id/apply-job',
      name: 'apply-job',
      component: ApplyJob
    },
    {
      path: '/myjobs',
      name: 'myjobs',
      component: MyJobs
    },
    {
      path: '/pricing-plan',
      name: 'pricing-plan',
      component: PricingPlan
    },
    {
      path: '/candidates/search',
      name: 'candidates-search',
      component: Candidates
    },
    {
      path: '/candidate/:id/details',
      name: 'candidate-details',
      component: CandidateDetail
    },
    {
      path: '/terms-and-conditions',
      name: 'terms-and-conditions',
      component: TermsAndConditions
    },
    {
      path: '/about-us',
      name: 'about-us',
      component: AboutUs
    },
    {
      path: '/account-setting',
      name: 'account-setting',
      component: AccountSetting,
      redirect: '/account-setting/password-security',
      children: [
        {
          path: 'password-security',
          name: 'password-security',
          component: PasswordAndSecurity
        },
        {
          path: 'billing-methods',
          name: 'billing-methods',
          component: BillingMethods
        },
        {
          path: 'notification-settings',
          name: 'notification-settings',
          component: NotificationSetting
        },
        {
          path: 'tax-information',
          name: 'tax-information',
          component: TaxInformation
        },
        {
          path: 'membership',
          name: 'membership',
          component: Membership
        }
      ]
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    console.log(to);
    console.log(from);
    return { x: 0, y: 0 }
  }
})
