<template>
  <div class="profile">
      <CandidateProfile v-if="isCandidate"></CandidateProfile>
      <EmployerProfile v-if="isEmployer"></EmployerProfile>
  </div>
</template>

<script>
import CandidateProfile from '@/components/Profile/CandidateProfile'
import EmployerProfile from '@/components/Profile/EmployerProfile'
import { mapGetters } from 'vuex'

export default {
  name: 'profile',
  computed: {
    ...mapGetters(['getRole','isCandidate','isEmployer'])
  },
  components: {
      CandidateProfile,
      EmployerProfile
  }
}
</script>

