import * as configs from "../configs";

class Storage {
    constructor() {

    }
    get(key, defaultValue) {
        var value = localStorage.getItem(key, defaultValue);
        return value;
    }

    set(key, value) {
        localStorage.setItem(key, value);
    }

    remove(key) {
        localStorage.removeItem(key);
    }

    removeAll() {
        localStorage.clear()
    }

    saveToken(value) {
        this.set(configs.token, value);
    }

    getToken() {
        return this.get(configs.token);
    }

    setExpireDate(value) {
        this.set(configs.expireDate, value);
    }

    getExpireDate() {
        return this.get(configs.expireDate);
    }

    setUserData(value) {
        this.set(configs.userInfo,value);        
    }

    getUserData() {
        let user = this.get(configs.userInfo);
        if(user) return JSON.parse(user)
        return null
    }

    deleteAuthData() {
        this.remove(configs.token);
        this.remove(configs.expireDate);
    }

    deleteUserData() {
        this.remove(configs.userInfo);
    }

    IsJsonString(str) {
        try {
            JSON.parse(str);
        } catch (e) {
            return false;
        }
        return true;
    }
}

const storage = new Storage();

export default storage;